a {
  text-decoration: none;
}

.contBarraMovil {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contBotonesDesplegables {
  display: flex;
  justify-content: flex-end;
  background-color: aqua;
}
.botonDrop {
  height: 45px;
  width: 80px;
}
.contBotonesFix {
  min-width: 0 !important;
  background: transparent;
}
.botonDesplegable {
  height: 40px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(141, 255, 255);
  background-color: rgb(0, 17, 17);
  border: 3px solid rgb(1, 255, 255);
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
}
.padding0 {
  padding: 0px !important;
}
.margin0 {
  margin: 0px !important;
}

.col-ImgBarDesplegable {
  background-color: rgb(39, 65, 65);
}

.contImgBarra {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: rgb(0, 19, 27);
}

.col-ImgBarraNav {
  width: 4.33333333%;
}

.contenedor-nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.imagenBarra {
  height: 60px;
  margin: 5px 10px 5px 10px;
  border: 2px solid rgb(1, 255, 255);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.BotonCambioPag {
  margin: 0px 3px 0px 3px;
  padding: 6px 3px 6px 3px;
  display: flex;
  align-items: center;
  color: rgb(141, 255, 255);
  background-color: rgb(0, 17, 17);
  border: 3px solid rgb(1, 255, 255);
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
}